const initialStateWCV2 = {
    WCV2Obj:{}
}
const ReducerFunkyStoreWCV2 = (state=initialStateWCV2,action)=>{
    if(action.payLoad){
        switch(action.type){
            case "wc/v2/":{
                // console.log("this is WCV2");
                state = {...state,WCV2Obj:action.payLoad};
                break;
            }
            default: return state
        }
    }
    return state;
}
export default ReducerFunkyStoreWCV2;   


