const initialState = {
    WPJSONObj:{},
    WPJSONNameSpace:[]
}
const ReducerWPJSON =(state=initialState,action)=>{
    // const newState = {...state};
 if(action.payLoad){
    switch(action.type){
        case "CompleteJsonData":{
        //  state.WPJSON = action.data;
        // console.log(action.payLoad);
         state = {...state,WPJSONObj:action.payLoad.data,WPJSONNameSpace:action.payLoad.data.namespaces};
         break;
        }
        default: return state;
    }
 }
 return state;
}
export default ReducerWPJSON;  


