import React from 'react';
import './FunkyWorld.css';
import {Route,Switch,withRouter,NavLink} from 'react-router-dom';
import {connect} from 'react-redux';
import store from './../Redux/Store/StoreFunkyWorld';
import FunkyWorldContainer from './FunkyWorldContainer/FunkyWorldContainer';
class FunkyWorld extends React.Component {
    componentWillReceiveProps(nextProps){
        // console.log("this is My Next Props",nextProps.completeJSONWithNameSpace.WPJSONObj.routes);
        var key = "?consumer_key=ck_fef0cba6ad9375d009c7f264f7d2176935d01ab9&consumer_secret=cs_08ce17479dee8a322534dc1ec5c4459556d1f578";
        var completeUrl = "https://funkyworld.in/wp-json/";
//==================================================================
  //--> This portion of code if for converting object into arrays of arrayall arrays contain key at 0th position and value at 1th position this all will be done with the help of Object.entries(); gives [["key",values],[],[]...]
  if(nextProps.completeJSONWithNameSpace.WPJSONObj){
    //   console.log('-->',nextProps.completeJSONWithNameSpace.WPJSONObj.routes);
    //   console.log(Object.entries(nextProps.completeJSONWithNameSpace.WPJSONObj.routes));
    Object.entries(nextProps.completeJSONWithNameSpace.WPJSONObj.routes).map((route)=>{
        // console.log("this is My routes",route);
        //--> choose different routing using switch:case statement
        switch(route[0]){
            case "/wp/v2/pages":{
    //this case is going to ReducerFunkyStroeWPV2.js and case: "/wp/v2/pages"
                // console.log("this is wpv2",route[0],route[1]._links.self);
                fetch(route[1]._links.self+'/?per_page=100&'+key).then(repsonse=>repsonse.json()).then((json)=>{
                    // console.log("this is Bipoii",json);
                    store.dispatch({
                        type:"/wp/v2/pages",
                        payLoad:{
                            data:json
                        }
                    });
                });
                break;
            }
            case "/wc/v1/products":{
                // console.log("this is wcv1",route[0],route[1]._links.self);
                break;
            }
            case "/wc/v2/products":{
                // console.log("this is wcv2",route[0],route[1]._links.self);
                break;
            }
            // default:console.log("hello");
        }
    });
  }
//==================================================================
    }
    render(){
        return (<div className="FunkyWorld">
            <FunkyWorldContainer/>
        </div>);
    }
}
function mapStateToProps(State){
// // console.log("this is application",State);
return {completeJSONWithNameSpace:State.WPJSON}

}
export default withRouter(connect(mapStateToProps)(FunkyWorld));
