const initialStateWCV2 = {
    WCV1Obj:{}
}
const ReducerFunkyStoreWCV1 = (state=initialStateWCV2,action)=>{
    if(action.payLoad){
        switch(action.type){
            case "wc/v1/":{
                // console.log("this is WCV1");
                state = {...state,WCV1Obj:action.payLoad};
                break;
            }
            default: return state;
        }
    }
    return state;
}
export default ReducerFunkyStoreWCV1;  


