import React from 'react';
import './Main.css';
import {Route,Switch,withRouter,NavLink} from 'react-router-dom';
import {connect} from 'react-redux';
import MainCarousel from './Maincarousel/MainCarousel';
import Parser from 'html-react-parser';
import MainSecond from './MainSecond/MainSecond';
import MainRightSide from './MainRightSide/MainRightSide';
var a=1;
var MainRight ;
class Main extends React.Component {
    constructor(){
        super();
      this.state = {LandingPageWPV2:{}};   
    }
    componentWillReceiveProps(nextProps){
        this.setState({LandingPageWPV2:nextProps.LandingPageWPV2});
        MainRight = nextProps.location.pathname==='/'?console.log("this '/' route is rendered"):<MainRightSide/>;
    }
    render(){
      var  LandingPgAllRouters = !isEmpty(this.state.LandingPageWPV2)?
        this.state.LandingPageWPV2.WPV2PagesObj.data.map(ele=>( <Route exact path={"/"+ele.slug+"/"} render={()=>(
            // --> here apply dynamically classes
            <div className={"MainTo"+ele.slug} >
                <h2>{ele.title.rendered}</h2>
                <p>{Parser(ele.content.rendered)}</p>
            </div>
        )}></Route> )               
        ):<h3>Data is Coming...</h3>;
        return(<div className="Main">
            <div className="row">
            <Switch>
                <Route  exact path="/" render={()=>(<div><MainCarousel/><MainSecond/></div>)}/>
                <div className="MainLeft col-9">{LandingPgAllRouters}</div>
            </Switch>
            <div className="MainRight  col-3">
            {MainRight}
            </div>
            </div>

        </div>);
    }
}
function mapStateToProps(State){
        if(State.WPV2.WPV2PagesObj.data)
    return {LandingPageWPV2:State.WPV2};
        // console.log('This is =',State.WPV2);
} 
function isEmpty(obj){
    for(var ele in obj){
        if(obj.hasOwnProperty(ele)) return false;
    }
    return true;
}
export default withRouter(connect(mapStateToProps)(Main)); 


