import React from 'react';
import './App.css';
import {BrowserRouter} from 'react-router-dom';
import FunkyWorld from './MainContainer/FunkyWorld';
import store from './Redux/Store/StoreFunkyWorld';
class App extends React.Component {
  constructor(){
    super();
    this.state = {};
  }
  componentWillMount(){
    // console.log("I am App outside call");
    var key = '?consumer_key=ck_fef0cba6ad9375d009c7f264f7d2176935d01ab9&consumer_secret=cs_08ce17479dee8a322534dc1ec5c4459556d1f578';
    var completeJsonUrl = 'https://funkyworld.in/wp-json/';
    fetch(completeJsonUrl+key).then(response=>{
      if(response.status === 200 && response.ok === true){
        return response.json();
      }
    }).then(json=>{
      this.setState({data:json});
      store.dispatch({
        type:'CompleteJsonData',
        payLoad:{
          data:json
        }
      });
    });
  }
  render(){
    var ram = this.state.data?<FunkyWorld/>:<div>
      <img id = "loadingImg" src="http://www.grasphub.com/wp-content/uploads/2013/12/Preloader_5.gif" alt="Girl in a jacket" width="100" height="100"/>

    </div>; 
//======================================================================================
// This is loading logo


//---------------------------------------------------------------------------------------
    return (<div>
      <div className="container-fixed App">
      <BrowserRouter>
      {ram}
      {/* <FunkyWorld/> */}
      </BrowserRouter>
      </div>
    </div>);
  }
}
export default App; 


