import {combineReducers} from 'redux';
import ReducerWPJSON from './ReducerWPJSON';
import ReducerFunkyStoreWCV1 from './ReducerFunkyStoreWCV1';
import ReducerFunkyStoreWCV2 from './ReducerFunkyStoreWCV2';
import ReducerFunkyStoreWPV2 from './ReducerFunkyStoreWPV2';
const Reducers = combineReducers({WPJSON:ReducerWPJSON,WCV1:ReducerFunkyStoreWCV1,WCV2:ReducerFunkyStoreWCV2,WPV2:ReducerFunkyStoreWPV2});
// const Reducers = combineReducers({WPJSON:ReducerWPJSON,WPV2:ReducerFunkyStoreWPV2});
export default Reducers;  


