const initialStateWPV2 = {
    WPV2Obj:{},
    WPV2PagesObj:{}
}
const ReducerFunkyStoreWPV2 = (state=initialStateWPV2,action)=>{
    if(action.payLoad){
        switch(action.type){
            case "/wp/v2/pages":{
                // console.log("this is WpV2",action.payLoad);
                state = {...state,WPV2PagesObj:action.payLoad};
                break;
            }
            default : return state;
        }
    }
return state;
}
export default ReducerFunkyStoreWPV2;

  


