import React from 'react';
import './MainRightSide.css';
import {NavLink} from 'react-router-dom';
class MainRightSide extends  React.Component {
    render(){
        return (<div className="MainRightSide">
                  <form className="form" action="">
                    <div className="form-group">
                        <input type="email" className="form-control" id="email" placeholder="Email"/>
                    </div>
                <button  type="submit" className="btn btn-dark btn-md btn-block">Subscribe</button>
                <div className="form-group">
                <br/>
                        <input type="search" className="form-control" id="search" placeholder="Search here..."/>
                    </div>
                </form>
            <NavLink to="/register/">Register</NavLink><br/>
            <NavLink to="/log-in/">Log-In</NavLink><br/>
            <NavLink to="/entries/">Entries RSS</NavLink><br/>
            <NavLink to="/comments/">Comments RSS</NavLink><br/>
            <NavLink to="/help/">WordPress.org</NavLink><br/>
            
            </div>);
    }
}
export default MainRightSide;



